 body,
 html {
   width: 100%;
   height: 100%;
 }
 
 body {
  font-family: 'Quicksand', sans-serif !important;
 }
 
 .btn-xl {
   padding: 1.25rem 2.5rem;
 }
 
 .content-section {
   padding-top: 7.5rem;
   padding-bottom: 7.5rem;
 }
 
 .content-section-heading h2 {
   font-size: 3rem;
 }
 
 .content-section-heading h3 {
   font-size: 1rem;
   text-transform: uppercase;
 }

 .mock-link {
   cursor: pointer;
 }
 
 h1,
 h2,
 h3,
 h4,
 h5,
 h6 {
   font-weight: 700;
 }
 
 .text-faded {
   color: rgba(255, 255, 255, 0.7);
 }
 
 /* Map */
 .map {
   height: 30rem;
 }
 
 @media (max-width: 992px) {
   .map {
     height: 75%;
   }
 }
 
 .map iframe {
   pointer-events: none;
 }
 
 .scroll-to-top {
   position: fixed;
   right: 15px;
   bottom: 15px;
   width: 50px;
   height: 50px;
   text-align: center;
   color: white;
   background: rgba(52, 58, 64, 0.5);
   line-height: 45px;
 }
 
 .scroll-to-top:focus, .scroll-to-top:hover {
   color: white;
 }
 
 .scroll-to-top:hover {
   background: #343a40;
 }
 
 .scroll-to-top i {
   font-weight: 800;
 }
 
 .masthead {
   min-height: 30rem;
   position: relative;
   display: table;
   width: 100%;
   height: auto;
   padding-top: 8rem;
   padding-bottom: 8rem;
   background: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 255, 0.1)), to(rgba(255, 255, 255, 0.1))), url("/img/CommandCenter.jpg");
   background: linear-gradient(90deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%), url("/img/CommandCenter.jpg");
   background-position: center center;
   background-repeat: no-repeat;
   background-size: cover;
 }
 
 .masthead h1 {
   font-size: 4rem;
   margin: 0;
   padding: 0;
 }
 
 @media (min-width: 992px) {
   .masthead {
     height: 100vh;
   }
   .masthead h1 {
     font-size: 5.5rem;
   }
 }
 
 /* Side Menu */
 #sidebar-wrapper {
   position: fixed;
   z-index: 2;
   right: 0;
   width: 250px;
   height: 100%;
   -webkit-transition: all 0.4s ease 0s;
   transition: all 0.4s ease 0s;
   -webkit-transform: translateX(250px);
   transform: translateX(250px);
   background: #1D809F;
   border-left: 1px solid rgba(255, 255, 255, 0.1);
 }
 
 .sidebar-nav {
   position: absolute;
   top: 0;
   width: 250px;
   margin: 0;
   padding: 0;
   list-style: none;
 }
 
 .sidebar-nav li.sidebar-nav-item a {
   display: block;
   text-decoration: none;
   color: #fff;
   padding: 15px;
 }
 
 .sidebar-nav li a:hover {
   text-decoration: none;
   color: #fff;
   background: rgba(255, 255, 255, 0.2);
 }
 
 .sidebar-nav li a:active,
 .sidebar-nav li a:focus {
   text-decoration: none;
 }
 
 .sidebar-nav > .sidebar-brand {
   font-size: 1.2rem;
   background: rgba(52, 58, 64, 0.1);
   height: 80px;
   line-height: 50px;
   padding-top: 15px;
   padding-bottom: 15px;
   padding-left: 15px;
 }
 
 .sidebar-nav > .sidebar-brand a {
   color: #fff;
 }
 
 .sidebar-nav > .sidebar-brand a:hover {
   color: #fff;
   background: none;
 }
 
 #sidebar-wrapper.active {
   right: 250px;
   width: 250px;
   -webkit-transition: all 0.4s ease 0s;
   transition: all 0.4s ease 0s;
 }
 
 .menu-toggle {
   position: fixed;
   right: 15px;
   top: 15px;
   width: 50px;
   height: 50px;
   text-align: center;
   color: black;
   background: rgba(52, 58, 64, 0.5);
   line-height: 50px;
   z-index: 999;
 }
 
 .menu-toggle:focus, .menu-toggle:hover {
   color:black;
 }
 
 .menu-toggle:hover {
   background: #343a40;
 }
 
 .service-icon {
   height: 7rem;
   width: 7rem;
   display: block;
   line-height: 7.5rem;
   font-size: 3.25rem;
 }
 
 .callout {
   padding: 15rem 0;
   background: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 255, 0.1)), to(rgba(255, 255, 255, 0.1))), url("/img/SLCPDIncidents.gif");
   background: linear-gradient(90deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%), url("/img/SLCPDIncidents.gif");
   background-position: center center;
   background-repeat: no-repeat;
   background-size: cover;
 }
 .vh-100 {
   height: 100vh;
 }
 .white-bg-faded {
   background-color: rgba(255, 255, 255, 0.8);
 }
 
